<template>
	<view class="healthsecna">
		<view class="healthcont">
			<view class="examinationcont">
				<text class="beijin">体检数据</text>
				<view class="getexamination" @tap="showNotice">
					一键获取体检信息
				</view>
			</view>
		</view>
		<view class="treatment">
			<view class="treatmentcont">
				<view class="programme">
					<text class="beijin">治疗方案</text>
					<view class="proright"  @click="goTreat">
						<image src="../../static/mine/fangxiang.png" mode=""></image>
					</view>
				</view>
				<view class="box">
					<view class="articleItem" v-for="(prolist,index) in treatmentList" :key="index" @click="ArticleDetail(prolist.id,treatIds,'治疗方案')">
							<image class="photo" :src="prolist.img" mode=""></image>
							<view class="articleBg" style="overflow:hidden; white-space:nowrap; text-overflow:ellipsis;">
								{{prolist.articleTitle}}
							</view>
					</view>
				</view>
			</view>
			
			
			<view class="treatmentcont">
				<view class="programme">
					<text class="beijin">保健建议</text>
					<view class="proright" @click="goAdvice">
						<image src="../../static/mine/fangxiang.png" mode=""></image>
					</view>
				</view>
				<view class="box">
					<view class="articleItem" v-for="(prolist,index) in adviceList" :key="index" @click="ArticleDetail(prolist.id,adviceIds,'保健建议')">
							<image class="photo" :src="prolist.img" mode=""></image>
							<view class="articleBg" style="overflow:hidden; white-space:nowrap; text-overflow:ellipsis;">
								{{prolist.articleTitle}}
							</view>
					</view>
				</view>
			</view>
			
			
			
			<view class="doctor">
				<view class="doctorcont">
					<view class="doctortent">
						<text class="beijin">名医推荐</text>
						
					</view>
					<view class="portraitdis">
						<view class="portrait" v-for="(item,index) in doctorList" :key="index">
							<view class="portraitwid" @click="goDoctor(index)">
								<view style="display: flex;justify-content: center;margin-bottom: 18rpx;">
									<image class="famousdoctor" :src="item.picUrl" mode=""></image>
								</view>
								<view style="font-size: 24rpx;text-align: center;">
									{{item.doctorName}}
								</view>
								<view style="font-size: 24rpx;text-align: center;">
									专长: {{item.doctorDept}}
								</view>
							</view>
						</view>
					</view>
				</view>
			</view>
			
			
			<view class="doctor">
				<view class="doctorcont">
					<view class="doctortent">
						<text class="beijin">产品推荐</text>
						<view class="proright"  @click="goProduct">
							<image src="../../static/mine/fangxiang.png" mode=""></image>
						</view>
					</view>
					<view class="portraitdis">
						<view class="portrait" v-for="(item,index) in productList" :key="index" @click="goodsDetail(item.id)">
							<view class="portraitwid" >
								<view class="producttype">
									<view class="producttycont">
										<view class="producttys">
											<image class="producttyimg" :src="item.goodsSmallImage" mode=""></image>
										</view>
									</view>
								</view>
								<view style="font-size: 24rpx;text-align: center;margin-top: 10rpx;">
									{{item.goodsTypeName}}
								</view>
							</view>
						</view>
					</view>
				</view>
			</view>
			
			
			<view class="treatmentcont" v-show="false">
				<view class="programme">
					<text class="beijin">附近体检中心位置</text>
					<view class="proright">
						<image src="../../static/mine/fangxiang.png" mode=""></image>
					</view>
				</view>
				<view class="procontent">
					<image style="width: 100%;height: 300rpx;background:no-repeat;" src="" mode=""></image>
				</view>
			</view>
			
			
			<view class="treatmentcont">
				<view class="programme">
					<text class="beijin">文章推荐</text>
					<view class="proright" @click="goArticle">
						<image src="../../static/mine/fangxiang.png" mode=""></image>
					</view>
				</view>
				<view class="box">
					<view class="articleItem" v-for="(prolist,index) in articleList" :key="index" @click="ArticleDetail(prolist.id,articleIds,'好文')">
							<image class="photo" :src="prolist.img" mode=""></image>
							<view class="articleBg" style="overflow:hidden; white-space:nowrap; text-overflow:ellipsis;">
								{{prolist.articleTitle}}
							</view>
					</view>
				</view>
			</view>
		</view>
		
		<view class="cu-modal show" v-if="showModal">
			<view class="cu-dialog">
				<view class="cu-bar bg-white justify-end"> 
					<view class="content " style="background-color: #fff;">
						请选择要查看的报告
					</view>
					<view class="action" @tap="hideModal">
						<text class="cuIcon-close text-red"></text>
					</view>
				</view>
				<view class="" v-for="(item,index) in reportType" :key="index" v-if="item.English!='暂无'">
					<view class="reportList" @click="goDetail(item)">
						 <text>{{item.name}}</text>
						 <text class="cuIcon-right"></text>
					</view>
					<view class="detail" v-if="item.flag">
						<view class="">
							检测时间:{{item.checkTime}}
						</view>
						<view class="titleTop">
							<view class="titleItem" v-for="(theme,index) in title" :key="index">
								{{theme}}
							</view>
						</view>
						<view class="reportItem" v-for="(data,index) in item.reportData" :key="index">
							<view class="titleItem">
								{{data.name}}
							</view>
							<view class="titleItem">
								{{data.result}}
							</view>
							<view class="titleItem">
								{{data.danWei}}
							</view>
							<view class="titleItem">
								{{data.reference}}
							</view>
						</view>
						<view class="" style="background-color: #cef3df;padding: 20rpx;">
							<view class="" style="text-align: left;" v-if="index!=5&&index!=6&&index!=7&&index!=9&&index!=10">
								体检结果:{{item.conclusion}}
							</view>
							<view class=""  style="text-align: left;" v-if="index!=6&&index!=9">
								{{item.suggest}}
							</view>
						</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		mapState,
		mapMutations,
	} from 'vuex';
	import {postServer} from '../../common/static-data.js'
	import {getNewData,getOldData,getAdvice} from '../../API/healthy/index.js'
	export default {
		data() {
			return {
				//reportList:[],
				idArr:[],
				showModal:false,
				bloodPressure:{},
				adviceList:[],
				treatmentList:[
				],
				doctorList:[
				],
				productList:[
				],
				reportType:[
				 {name:'身高体重记录表',flag:false,English:'',checkTime:'',conclusion:'',suggest:'',reportData:
				 [
				 {name:'身高',result:'',danWei:'cm',reference:'/'},
				 {name:'体重',result:'',danWei:'kg',reference:'/'},
				 {name:'体重指数',result:'',danWei:'kg/m²',reference:''}
				   ]
				 },
				 {name:'血压报告',flag:false,English:'',checkTime:'',conclusion:'',suggest:'',reportData:
				 [
				 {name:'高压',result:'',danWei:'mmHg',reference:''},
				 {name:'低压',result:'',danWei:'mmHg',reference:''},
				 {name:'脉率',result:'',danWei:'bgm',reference:''},
				   ]
				 },
				 {name:'血氧报告',flag:false,English:'',checkTime:'',conclusion:'',suggest:'',reportData:
					 [
					 {name:'血氧',result:'',danWei:'%',reference:''},
					 {name:'脉率',result:'',danWei:'bgm',reference:''}
					   ]
				 },
				 {name:'骨密度报告',flag:false,English:'',checkTime:'',conclusion:'',suggest:'',reportData:
					 [
					 {name:'声速值',result:'',danWei:'m/s',reference:'/'},
					 {name:'骨质量与同性别青年组平均BUA的比值',result:'',danWei:'bpm',reference:''},
					 {name:'骨质量与同年龄青年组平均BUA的比值',result:'',danWei:'bpm',reference:''}
					   ]
				 },
				 {name:'人体成分报告',flag:false,English:'',checkTime:'',conclusion:'',suggest:'',reportData:
					 [
						 
					 {name:'体脂肪率',result:'',danWei:'kg',reference:''},
					 {name:'体脂肪量',result:'',danWei:'%',reference:''},
					 {name:'非脂肪量',result:'',danWei:'kg',reference:''},
					 {name:'体内水分量',result:'',danWei:'kg',reference:''},
					 {name:'基础代谢',result:'',danWei:'kj',reference:''},
					 {name:'身体基础代谢年龄',result:'',danWei:'/',reference:''},
					 {name:'内脏脂肪等级',result:'',danWei:'/',reference:''},
					 {name:'骨质量',result:'',danWei:'kj',reference:''},
					 {name:'肌肉量',result:'',danWei:'kg',reference:''},
					 {name:'推定骨量',result:'',danWei:'kg',reference:''},
					 {name:'肌肉量等级',result:'',danWei:'/',reference:''},
					 {name:'肥胖程度',result:'',danWei:'%',reference:''},
					 {name:'体水分率',result:'',danWei:'%',reference:''},
					 {name:'肌肉率',result:'',danWei:'%',reference:''}
					   ]
				 },
				 {name:'体温报告',flag:false,English:'',checkTime:'',conclusion:'',suggest:'',reportData:
					 [
				    {name:'体温',result:'',danWei:'℃',reference:''},
					]
				 },
				 {name:'中医体质辨识',flag:false,English:'',checkTime:'',conclusion:'',suggest:'',reportData:[
					{name:'气虚质得分',result:'',danWei:'/',reference:''},
					{name:'阳虚质得分',result:'',danWei:'/',reference:''},
					{name:'阴虚质得分',result:'',danWei:'/',reference:''},
					{name:'痰湿质得分',result:'',danWei:'/',reference:''},
					{name:'湿热质得分',result:'',danWei:'/',reference:''},
					{name:'血瘀质得分',result:'',danWei:'/',reference:''},
					{name:'气郁质得分',result:'',danWei:'/',reference:''},
					{name:'特禀质得分',result:'',danWei:'/',reference:''},
					{name:'平和质得分',result:'',danWei:'/',reference:''},
					
				 ]},
				 {name:'血脂报告',flag:false,English:'',checkTime:'',conclusion:'',suggest:'',reportData:
					 [
					{name:'低密度脂蛋白',result:'',danWei:'mmol/L',reference:''}, 
					{name:'高密度脂蛋白',result:'',danWei:'mmol/L',reference:''},
					{name:'甘油三脂',result:'',danWei:'mmol/L',reference:''},
					{name:'总胆固醇',result:'',danWei:'mmol/L',reference:''},
					]
				 },
				 {name:'肺活量报告',flag:false,English:'',checkTime:'',conclusion:'',suggest:'',reportData:
					 [
					{name:'肺活量',result:'',danWei:'/',reference:''},
					{name:'肺活量预期值',result:'',danWei:'/',reference:''},
					{name:'肺活量预期占比',result:'',danWei:'/',reference:''},
					{name:'用力肺活量最好值',result:'',danWei:'/',reference:''},
					{name:'用力肺活量预期值',result:'',danWei:'/',reference:''},
					{name:'用力肺活量预期占比',result:'',danWei:'/',reference:''},
					{name:'呼出气量',result:'',danWei:'/',reference:''},
					{name:'呼出时间',result:'',danWei:'/',reference:''},
					{name:'呼吸频率',result:'',danWei:'/',reference:''},
					{name:'潮气量',result:'',danWei:'/',reference:''},
					{name:'最大呼气中段流量最好值',result:'',danWei:'/',reference:''},
					{name:'最大呼气流量预期值',result:'',danWei:'/',reference:''},
					{name:'最大呼气流量预期占比',result:'',danWei:'/',reference:''},
					{name:'最大吸气流量最好值',result:'',danWei:'/',reference:''},
					{name:'最大吸气流量预期值',result:'',danWei:'/',reference:''},
					{name:'最大吸气流量预期占比',result:'',danWei:'/',reference:''}
					]
				 },
				 {name:'尿常规报告',flag:false,English:'',checkTime:'',conclusion:'',suggest:'',reportData:
					 [
					{name:'亚硝酸盐',result:'',danWei:'umol/L',reference:'/'},
					{name:'PH值',result:'',danWei:'/',reference:'/'},
					{name:'葡萄糖',result:'',danWei:'mmol/L',reference:'/'},
					{name:'蛋白质',result:'',danWei:'g/L',reference:'/'},
					{name:'潜血/隐血',result:'',danWei:'cells/ul',reference:'/'},
					{name:'酮体',result:'',danWei:'mmol/L',reference:'/'},
					{name:'胆红素',result:'',danWei:'umol/L',reference:'/'},
					{name:'尿胆原',result:'',danWei:'umol/L',reference:'/'},
					{name:'抗坏血酸',result:'',danWei:'mmol/L',reference:'/'},
					{name:'白细胞',result:'',danWei:'cells/ul',reference:'/'},
					{name:'微白蛋白',result:'',danWei:'cells/ul',reference:'/'},
					{name:'肌酐',result:'',danWei:'mmol/L',reference:'/'},
					{name:'尿酸',result:'',danWei:'mmol/L',reference:'/'},
					]
				 },
				 {name:'血尿酸报告',flag:false,English:'',checkTime:'',conclusion:'',suggest:'',reportData:
					 [
					{name:'血尿酸测量值',result:'',danWei:'/',reference:''},
					]
				 },
				 {name:'血糖报告',flag:false,English:'',checkTime:'',conclusion:'',suggest:'',reportData:
					 [
					{name:'血糖类型',result:'',danWei:'/',reference:'/'},
					{name:'血糖值',result:'',danWei:'/',reference:''},
					]
				 },
				],
				title:['项目名称','结果','单位','参考值'],
				articleList:[],
				treatIds:[],
				adviceIds:[],
				articleIds:[]
			}
		},
		computed: {
			...mapState(['token']),
		},
		onLoad() {

		},
		onShow() {
			getApp().globalData.index=3
             this.getAdviceList()
		},
		methods: {
			ArticleDetail(id,idArr,type){
				if(type=='治疗方案'){
					uni.navigateTo({
						url:'../goodwriiting/articledetail?id='+id+ '&ids=' + JSON.stringify(idArr)+'&type=治疗方案'
					})
				}else if(type=='保健建议'){
					uni.navigateTo({
						url:'../goodwriiting/articledetail?id='+id+ '&ids=' + JSON.stringify(idArr)+'&type=保健建议'
					})
				}else if(type=='好文'){
					uni.navigateTo({
						url:'../goodwriiting/articledetail?id='+id+ '&ids=' + JSON.stringify(idArr)+'&type=好文'
					})
				}
				
			},
            goDoctor(index){
				uni.navigateTo({
					url:'../doctorPage/doctorPage?index='+index
				})
			},
			goProduct(){
				uni.navigateTo({
					url:'../recommendation/product'
				})
			},
			goTreat(){
				uni.navigateTo({
					url:'../treatment/treatment'
				})
			},
			goArticle(){
				uni.navigateTo({
					url:'../recommendation/article'
				})
			},
			goAdvice(){
				uni.navigateTo({
					url:'../recommendation/advice'
				})
			},
			goodsDetail(id){
				uni.navigateTo({
					url:'../seckill/seckdeil?id='+id
				})
			},
		async getAdviceList(){
			const res=await getAdvice(this.token)
			console.log(res)
			this.treatmentList=res.treatList.slice(0,3)
			this.treatIds=res.treatIds
			this.adviceList=res.adviceList.slice(0,3)
			this.adviceIds=res.adviceIds
			//this.idArr=res.adviceIds
			console.log(this.adviceList)
			this.doctorList=res.doctorRecommendList
			this.productList=res.productRecommenList.slice(0,4)
			this.articleList=res.articleList.slice(0,3)
			this.articleIds=res.articleIds
		},
		async  showNotice(){
				this.showModal=true
			/*	const res=await uni.request({
					url: postServer+'/ThirdPartyCall/physicalExaminationParameters',
					data:'this.token'
				}) */
				const res=await getOldData(this.token)
				console.log(res)
			
				this.reportType[0].English=res.heightAndWeight //身高体重
				//this.reportType[1].English=res.measurements  //测量
				this.reportType[1].English=res.bloodPressure //血压
				this.reportType[2].English=res.bloodOxygen  //血氧
				this.reportType[3].English=res.bonedensity  //骨密度
				this.reportType[4].English=res.bodyComposition //人体成分
				this.reportType[5].English=res.temperature  //体温
				this.reportType[6].English=res.tcm //中医药
				this.reportType[7].English=res.bloodFat  //血脂
				this.reportType[8].English=res.pulmonaryFunctionRecord //肺活量
			    this.reportType[9].English=res.urineRoutine  //尿常规
				this.reportType[10].English=res.uricAcid   //血尿酸
				this.reportType[11].English=res.bloodSugar  //血糖
				//this.reportType[11].English=res.ecg   //心电图
				//this.reportType[12].English=res.retinalHealth //视网膜健康
				//this.reportType.map(item=>{item.checkTime='';item.suggest=''})
				if(this.reportType[0].English!==null){
				this.reportType[0].reportData[0].result=res.heightAndWeight.height
				this.reportType[0].reportData[1].result=res.heightAndWeight.weight
				this.reportType[0].reportData[2].result=res.heightAndWeight.bmi
				this.reportType[0].reportData[2].result=res.heightAndWeight.bmiRef
				this.reportType[0].checkTime=res.heightAndWeight.examTime
			    this.reportType[0].conclusion=res.heightAndWeight.result
				this.reportType[0].suggest=res.heightAndWeight.suggestion 
				}
				
				if(this.reportType[1].English!==null){
				this.reportType[1].reportData[0].result=res.bloodPressure.height_pressure
				this.reportType[1].reportData[0].reference=res.bloodPressure.height_pressure_ref
				this.reportType[1].reportData[1].result=res.bloodPressure.low_pressure
				this.reportType[1].reportData[1].reference=res.bloodPressure.low_pressureRef
				this.reportType[1].reportData[2].result=res.bloodPressure.pulse
				this.reportType[1].reportData[2].reference=res.bloodPressure.pulse_ref
				this.reportType[1].checkTime=res.bloodPressure.exam_time
				this.reportType[1].conclusion=res.bloodPressure.result
				this.reportType[1].suggest=res.bloodPressure.suggestion 
				}
				
				if(this.reportType[2].English!==null){
				this.reportType[2].reportData[0].result=res.bloodOxygen.blood_oxygen
				this.reportType[2].reportData[0].reference=res.bloodOxygen.blood_oxygenRef
				this.reportType[2].reportData[1].result=res.bloodOxygen.pulse
				this.reportType[2].reportData[1].reference=res.bloodOxygen.pulse_ref
				this.reportType[2].checkTime=res.bloodOxygen.exam_time
				this.reportType[2].conclusion=res.bloodOxygen.result
				this.reportType[2].suggest=res.bloodOxygen.suggestion
				}
				
				if(this.reportType[3].English!==null){
				this.reportType[3].reportData[0].result=res.bonedensity.sos
				this.reportType[3].reportData[1].result=res.bonedensity.t_score
				this.reportType[3].reportData[1].reference=res.bonedensity.t_scoreRef
				this.reportType[3].reportData[2].result=res.bonedensity.z_score
				this.reportType[3].reportData[2].reference=res.bonedensity.z_scoreRef
				this.reportType[3].checkTime=res.bonedensity.exam_time
				this.reportType[3].conclusion=res.bonedensity.result
				this.reportType[3].suggest=res.bonedensity.suggestion
				}
				
				if(this.reportType[4].English!==null){
				this.reportType[4].checkTime=res.bodyComposition.examTime
				this.reportType[4].conclusion=res.bodyComposition.result
				this.reportType[4].suggest=res.bodyComposition.suggestion
				this.reportType[4].reportData[0].result=res.bodyComposition.fatPercentage
				this.reportType[4].reportData[0].reference=res.bodyComposition.fatPercentageRef
				this.reportType[4].reportData[1].result=res.bodyComposition.fat
				this.reportType[4].reportData[1].reference=res.bodyComposition.fatRef
				this.reportType[4].reportData[2].result=res.bodyComposition.noFat
				this.reportType[4].reportData[2].reference=res.bodyComposition.noFatRef
				this.reportType[4].reportData[3].result=res.bodyComposition.bodyWater
				this.reportType[4].reportData[3].reference=res.bodyComposition.bodyWaterRef
				this.reportType[4].reportData[4].result=res.bodyComposition.basicMetabolism
				this.reportType[4].reportData[4].reference=res.bodyComposition.basicMetabolismRef
				this.reportType[4].reportData[5].result=res.bodyComposition.metabolicAge
				this.reportType[4].reportData[6].result=res.bodyComposition.visceralFatRating //内脏脂肪等级
				this.reportType[4].reportData[6].reference=res.bodyComposition.visceralFatRatingRef
				this.reportType[4].reportData[7].result=res.bodyComposition.bone
				this.reportType[4].reportData[8].result=res.bodyComposition.muscle
				this.reportType[4].reportData[9].result=res.bodyComposition.boneMass
				this.reportType[4].reportData[9].reference=res.bodyComposition.boneMassRef
				this.reportType[4].reportData[10].result=res.bodyComposition.mslScore
				this.reportType[4].reportData[10].reference=res.bodyComposition.mslScoreRef
				this.reportType[4].reportData[11].result=res.bodyComposition.obesity
				this.reportType[4].reportData[11].reference=res.bodyComposition.obesityRef
				this.reportType[4].reportData[12].result=res.bodyComposition.wtrAmount
				this.reportType[4].reportData[12].reference=res.bodyComposition.wtrAmountRef
