                }
				
				this.reportType.forEach(item=>{
					for(var i in item){
						if(item[i]==null){
							item[i]='暂无'
						}else if(Array.isArray(item[i])){
							item[i].forEach(subItem=>{
								for(var key in subItem){
									if(subItem[key]==null){
										subItem[key]='暂无'
									}
								}
							})
						}
					}
				})
			},
			hideModal(){
				this.showModal=false
			},
			goDetail(item){
				item.flag=!item.flag
			}
		}
	}
</script>

